.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  background-color: rgba(0 0 0 / 0.5);
  visibility: hidden;
  opacity: 0;
  transition: .7s;
  transition-property: visibility, opacity;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 6;
}

.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__container {
  width: 100%;
  max-width: 50vw;
  padding: 35px;
  background-color: white;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
  box-sizing: border-box;
}

.popup__info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.popup__avatar {
  display: block;
  width: 40%;
  max-height: 30vh;
  object-fit: cover;
  object-position: top;
}

.popup__heading {
  margin: 0 0 20px;
  font-weight: 900;
  font-size: 24px;
  line-height: 1.21; 
}

.popup__birthday {
  margin: 0;
}

.popup__title {
  margin: 10px 0;
  font-weight: 500;
}

.popup__text {
  margin: 3px 0;
}

.popup__button {
  width: 40px;
  height: 40px;
  min-width: 10px;
  min-height: 10px;
  background-image: url('./../images/close.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  background-color: transparent;
  border: 0;
  font-size: 18px;
  line-height: 1.22;
  text-align: center;
  box-sizing: border-box;
  transform: translate(100%, -100%);
  transition: opacity .3s;
  outline: none;
  position: absolute;
  top: 0;
  right: 0;
}

@media screen and (min-width: 600px) and (min-height: 400px) {
  .popup__button {
    width: 40px;
    height: 40px;
    background-size: auto;
    transform: translate(100%, -100%);
    top: 0;
    right: 0;  
  }
  
  .popup__button:hover {
    opacity: .6;
    cursor: pointer;
  }
}

@media screen and (max-height: 399px) {
  .button_type_close-popup {
    width: 40px;
    height: 40px;
    transform: translate(135%, 0%);
    top: 0;
    right: 0;
  }
}
